exports.getprize = function() {
	API.getPrize({openid:config.userInfo.openid}).then((res)=>{
          if(res.code !== 0) {
				return ;
			}  
//			console.log(res);
			config.grade0=[];
			config.grade1=[];
			config.grade2={};
			$.each((res.data),(index,item)=>{
//				console.log(item,'item');
				switch (item.grade){
				case 0:
					config.grade0.push(item);
					break;
				case 1:
					config.grade1.push(item);
					break;
				case 2:
					config.grade2=item;
					break;
				}	
			})
//			console.log(config,'config');
			
			Popup.myprize.showprize();
		})
}
exports.deepCopy2=function(obj){
    if(typeof obj != 'object'){
        return obj;
    }
    var newobj = {};
    for ( var attr in obj) {
        newobj[attr] = util.deepCopy2(obj[attr]);
    }
    console.log(newobj);
    return newobj;
}
exports.deepCopy=function(obj){

    var o,i,j,k;  
    if(typeof(obj)!="object" || obj===null)return obj;  
    if(obj instanceof(Array))  
    {  
        o=[];  
        i=0;j=obj.length;  
        for(;i<j;i++)  
        {  
            if(typeof(obj[i])=="object" && obj[i]!=null)  
            {  
                o[i]=arguments.callee(obj[i]);  
            }  
            else  
            {  
                o[i]=obj[i];  
            }  
        }  
    }  
    else  
    {  
        o={};  
        for(i in obj)  
        {  
            if(typeof(obj[i])=="object" && obj[i]!=null)  
            {  
                o[i]=arguments.callee(obj[i]);  
            }  
            else  
            {  
                o[i]=obj[i];  
            }  
        }  
    }  
    return o;  

}
